@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255,255,255, 0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

body {
font-family: "Roboto Condesed" sans-serif;
}


/************************ NAVBAR *********************************/

nav {
  z-index: 2;
}

.logo {
  margin-top: 1rem;
  width: 16.25rem;
}

/*----- MENU LINKS --------*/
nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgba(255,255,255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

.navbar-nav > li > a.active,  .navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}
/************************ HEADER *********************************/

.header-wraper {

position: relative;
background: url(/Portoloio-React/static/media/bgimg.7dca5d9c.JPG) no-repeat;
background-size: cover;
background-position: center;
height:90vh

}

.main-info {

display: -webkit-flex;

display: flex;
position:absolute;
top: 0;
left: 0;
right: 0;
bottom: 0;
-webkit-flex-direction: column;
        flex-direction: column;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: center;
        align-items: center;
text-align:center;
font-family: "Roboto Condesed", sans-serif;
z-index: 1;
}

.main-info h1 {
  color: darkolivegreen;
  text-transform: uppercase;
}
/************************ TYPED TEXT *********************************/

.typed-text {

  font-size: 2rem;
}
.btn-main-offer {
border: 1px solid grey;
text-transform: uppercase;
border-radius: 5px;
padding: 10px 12px 12px 12px;
margin: 2rem;

}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: rgb(151, 111, 35);
  color: rgb(93, 93, 202);
  transition:  .2s ease-in-out;
}


.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}


/************************ ABOUT ME *********************************/
.photo-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.about-heading {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}

.appsh{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  text-align: center;
}



.art-img {width: 350px;
  height: 350px;
  padding: 8px;}

  .btn{
    font-size: 1.0rem;
    padding: 1rem 0;
    width: 30rem;
    text-align: center;
    border: 0.1rem solid rgb(16, 16, 141);
    margin-bottom: 4rem;
    margin-top: 1rem;
    text-decoration: none;
    color: rgb(41, 37, 49);
    background-color: rgb(174, 212, 221);
    display: block; 
    margin: auto;
}

.btn:hover {
    cursor: pointer;
    box-shadow: 0 0.4rem 1.4rem 0 rgba(15, 15, 83, 0.5);
    -webkit-transform: translateY(-0.1rem);
            transform: translateY(-0.1rem);
    transition: -webkit-transform 150ms;
    transition: transform 150ms;
    transition: transform 150ms, -webkit-transform 150ms;
}


a.btn {background-color: #bd655e;}
.wrapper {margin: auto;}


.buttonField {margin:0 auto;
display:block;        
width: 60%;}

#connect {width: 400px;
height: 200px;
margin-bottom: 5px;}

.contacts {
  background: rgba(255,255,255, 0);
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #f9ab00;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #f9ab00;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #fff;
  color: var(--primary-white);
  background: #f33501;
  background: var(--primary-hover-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: var(--primary-green);
}


@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }}
